___CSS_LOADER_EXPORT___.push([module.id, `.touch-info {
  margin-top: 3%;
  /* background-color: #3d3f46; */
  /* background-color: rgba(61, 63, 70, 0.5); */
  padding: 20px;
  border-radius: 8px;
  width: 450px;
  height: 200px;
  margin-bottom: 20px;

  display: flex;
  flex-direction: column;
  align-items: center; /* 중앙 정렬 */
  justify-content: center;
  font-family: 'f5';
  font-size: 13pt;
  
}

.touch-area {
  width: 420px;
  border-radius: 15px;
}

.touch-info h2 {
  font-size: 1.5em;
  color: #FFFFFF;
  /* text-align: left; */
  
}

.touch-bar {
  /* background-color: rgba(61, 63, 70, 0.5); */
  display: flex;
  align-items: center;
  margin: 5px 0;
  color: #FFFFFF;
}

.touch-bar span {
  flex: 1;
}

.progress-bar-wrapper {
  flex: 4;
  position: relative;
}

.custom-progress-bar .progress-bar {
  background-color: transparent !important;
  background-image: none !important;
}

.ticks {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
}

.tick {
  width: 4px;
  height: 100%;
  background-color: #3d3f46;
}


.touch-info-text {
  margin-top: 2px;
  margin-bottom: 5px;
  width: 450px;
  height: 50px;
  /* background-color: #D1D1D1; */
  text-align: center;
}

/* Rectangle */




